			getPro() {
				this.$axios.get(this.url_pro).then(res => {
					if (res.data.results.length >= 1) {
						this.waterList = res.data.results[0].water;
						this.sedimentList = res.data.results[0].sediment;
					}
				});
			},
			getSpaceGeographicalType() {
				this.$axios.get('/api/space_geographical_type/').then(res => {
					this.typeList = res.data.results;
					this.getFunctional();
					this.getDepthContour();
					this.getNavigation();
					this.getBath();
					this.getPreserves();
					this.getOutfall();
					this.getNuclear();
					// 图层加载顺序通过延迟运行方法实现
					setTimeout(() => {
						this.getSitePoint();
					}, 500);
				})
			},
            tiskCalendarBtn() {
                this.tiskCalendarShow = !this.tiskCalendarShow;
                if (this.tiskCalendarShow == true) {
                    this.MonitoringMonthValue = 'Jan';
                    this.changmonth('Jan')
                }
            },
            changmonth(val) {
                this.isScrollbar = false
                this.axios.get(this.url_risk + '?month=' + val).then(res => {
                    for (let v of res.data.results) {
                        v.MonitoringMonthValue = this.MonitoringMonthValue;
                    }
                    this.tiskCalendarData = res.data.results;
                    this.isScrollbar = true;
                }, err => {
                    console.log(err);
                })
            },
            isImgShow(url) {
                let data = [];
                let image = ''
                for (let i of url) {
                    image = i.images ? i.images.replace(/^http:\/\/[^/]{3,255}/g, 'http://140.206.129.162:8101') : ''
                    data.push(image)

                }
                this.imgSrc = data;
                this.dialogVisible = true;
            },
            closeViewer() {
                this.dialogVisible = false
            },
            sedimentFormatter(row, column, cellValue, index) {
                for (let i of this.sedimentData) {
                    if (i.label == column.label) {
                        for (let v in i.value) {
                            if (row.param == v) return i.value[v]
                        }
                    }
                }
            },
            waterFormatter(row, column, cellValue, index) {
                for (let i of this.waterData) {
                    if (i.label == column.label) {
                        for (let v in i.value) {
                            if (row.param == v) return i.value[v]
                        }
                    }
                }
            },
            lifeFormatter(row, column, cellValue, index) {
                for (let i of this.lifeData) {
                    if (i.label == column.label) {
                        for (let v in i.value) {
                            if (row.assessment == v) return i.value[v]
                        }
                    }
                }
            },
