						this.siteActiveName == "diversitys" ? "diversity" : this.siteActiveName;
				this.$axios.get(url).then(
						res => {
							let isTrue = false;
							let paramList = [];
							if (this.siteActiveName == "diversitys") {
								this.lifeData = [];
								for (let item of res.data.results) {
									this.lifeData.push({
										id: item.id,
										label: item.BatchName,
										value: item.site_data
									});
								}
								this.lifeTable = res.data.results[0].site_data;
							} else if (this.siteActiveName == "water") {
								paramList = JSON.parse(JSON.stringify(this.waterList));
								paramList.unshift({name: "评价结果", param: "result"});
								this.waterData = [];
								for (let i of res.data.results) {
									for (let v of i.site_data) {
										isTrue = v.water ? true : false;
									}
									if (isTrue) {
										this.waterData.push({
											id: i.id,
											label: i.BatchName,
											value: i.site_data[0].water[0]
										});
									} else {
										this.waterData.push({
											id: i.id,
											label: i.BatchName,
											value: []
										});
									}
								}
								if (this.waterData.length >= 1) {
									for (let item of paramList) {
										this.waterTable.push({param: item.param, name: item.name});
									}
								} else {
									this.waterTable = [];
								}
							} else {
								paramList = JSON.parse(JSON.stringify(this.sedimentList));
								paramList.unshift({name: "评价结果", param: "result"});
								this.sedimentData = [];
								for (let i of res.data.results) {
									for (let v of i.site_data) {
										isTrue = v.sediment ? true : false;
									}
									if (isTrue) {
										this.sedimentData.push({
											id: i.id,
											label: i.BatchName,
											value: i.site_data[0].sediment[0]
										});
									} else {
										this.sedimentData.push({
											id: i.id,
											label: i.BatchName,
											value: []
										});
									}
								}
								if (this.sedimentData.length >= 1) {
									for (let item of paramList) {
										this.sedimentTable.push({param: item.param, name: item.name});
									}
								} else {
									this.sedimentTable = [];
								}
							}
						},
						err => {
							console.log(err);
						}
				);
			},
