            },
            menuOperation() {
                this.isMeun = !this.isMeun;
                this.isFilter = false;
                let isJudge = this.navList[1].children
                if (!isJudge[0].checked && !isJudge[1].checked && !isJudge[2].checked) {
                    this.basemap.removeLayer(this.mapLayers.KrigingLayer)
                }
                if (this.isMeun == true) {
                    for (let i of this.navList[1].children) {
                        if (i.checked == true) this.isFilter = true;
                    }
                }
            },
			routerLink(actionFunc, checked, layername, index, name) {
				if (checked) {
					if (this.krigingLayers !== null) {
						this.basemap.removeLayer(this.krigingLayers);
					}
					if (name == "水质评价数据") {
						if (this.krigingLayers !== null) {
							this.basemap.removeLayer(this.krigingLayers);
						}
						this.parameterSelect = JSON.parse(JSON.stringify(this.waterList));
						this.parameterSelect.unshift({name: "评价结果", param: "result"});
						for (let i of this.navList[1].children) {
							if (i.name != name) i.checked = false;
						}
						if (this.parameterSelect.length >= 1)
							this.parameterValue = this.parameterSelect[0].param;
						this.displayModeValue = this.displayModeSelect[0].id;
						this.isFilter = true;
						this[actionFunc](name);
						this.environmentalMonitoringValue = name;
					} else if (name == "沉积物评价结果") {
						if (this.krigingLayers !== null) {
							this.basemap.removeLayer(this.krigingLayers);
						}
						this.parameterSelect = JSON.parse(JSON.stringify(this.sedimentList));
						this.parameterSelect.unshift({name: "评价结果", param: "result"});
						for (let i of this.navList[1].children) {
							if (i.name != name) i.checked = false;
						}
						if (this.parameterSelect.length >= 1)
							this.parameterValue = this.parameterSelect[0].param;
						this.displayModeValue = "interpolation";
						this.isFilter = true;
						this[actionFunc](name);
						this.environmentalMonitoringValue = name;
					} else if (name == "生物多样性评价") {
						for (let i of this.navList[1].children) {
							if (i.name != name) i.checked = false;
						}
						this.parameterSelect = [
							{
								param: "PhytoplanktonZooplanktonMesh",
								name: "浮游植物"
							},
							{
								param: "ZooplanktonAmount",
								name: "浮游动物"
							},
							{
								param: "BenthosRation",
								name: "浅海大型底栖生物"
							},
							{
								param: "TidalZoneBiologyRation",
								name: "潮间带生物"
							}
						];
						this.environmentalMonitoringValue = name;
						this.parameterValue = this.parameterSelect[0].param;
						this.displayModeValue = "interpolation";
						this.isFilter = true;
						this[actionFunc](name);
					}
					if (this.mapLayers[layername] !== undefined) {
						this.basemap.addLayer(this.mapLayers[layername]);
						this.stationShow = true;
						if (layername === "siteLayer") {
							this.siteOn = true;
						} else if (layername === "functionalLayer") {
							this.basemap.removeLayer(this.mapLayers[layername]);
							this.getFunctional();
						}
					} else {
						this[actionFunc](name);
					}
				} else {
					this.basemap.removeLayer(this.krigingLayers);
					this.dataBar = false;
					this.basemap.removeLayer(this.mapLayers[layername]);
					// 图例控制
					if (layername === "functionalLayer") {
						this.legendShow = false;
					}
				}
				let isJudge = this.navList[1].children;
				if (!isJudge[0].checked && !isJudge[1].checked && !isJudge[2].checked) {
					this.isFilter = false;
					this.parameterValue = "";
					this.displayModeValue = "interpolation";
				}
			},
