            /**
             * @author Qtvz
             * @date 2019/11/28
             */
			getFunctional() {
				const tiledSource = new TileWMS({
					url: this.functionalUrl,
					params: {
						LAYERS: "Functional:福建功能区",
						tiled: true
					},
					serverType: "geoserver",
					crossOrigin: "anonymous"
				});
				const tiled = new Tile({
					source: tiledSource
				});
				this.loading = true;
				// this.basemap.addLayer(tiled);
				const layerArray = this.basemap.getLayers();
				layerArray.insertAt(1, tiled);
				this.mapLayers["functionalLayer"] = tiled;
				this.legendShow = true;
				const resolution = this.basemap.getView().getResolution();
				const graphicUrl = tiledSource.getLegendUrl(resolution);
				if (graphicUrl !== "") {
					setTimeout(() => {
						const img = this.$refs.legend;
						img.src = graphicUrl;
					}, 50);
				} else {
					this.$alert("未能从geoserver获取到图例，请检查连接", "提示");
				}
				this.loading = false;
			},
            /**
             * @author Qtvz
             * @date 2019/11/12
             **/
			getNavigation() {
				let line = [];
				let layerGroup = [];
				let name = "领海基线";
				let jsonData = ''
				for (let i of this.typeList) {
					if (i.name == name) jsonData = i.file
				}
				jsonData = jsonData.replace(/^http:\/\/[^/]{3,255}/g, "");
				this.$axios.get(jsonData).then(res => {
					let lineStringFeature;
					const features = res.data.features;
					// const num = features.length;
					// console.log("一共" + num + "条线");
					for (const feature of features) {
						const name = feature.attributes.NAME;
						const paths = feature.geometry.paths;
						const linePath = [paths, name];
						line.push(linePath);
					}
					for (const lineString of line) {
						for (const coordinate of lineString[0]) {
							lineStringFeature = new Feature({
								geometry: new LineString(coordinate),
								geometryName: lineString[1],
								geometryName_: "navigation"
							});
							const lineSource = new sVector();
							lineSource.addFeature(lineStringFeature);
							const layer = new Vector({
								title: lineString[1],
								source: lineSource,
								style: new Style({
									stroke: new Stroke({
										color: "#1f33ff",
										width: 2,
										lineDash: [1, 2, 3, 4, 5, 6]
									})
								})
							});
							layerGroup.push(layer);
						}
					}
					this.nvStringLayer = new LayerGroup({
						title: "领海基线",
						layers: layerGroup
					});
					console.log(this.nvStringLayer);
					this.basemap.addLayer(this.nvStringLayer);
					this.mapLayers["navigationLayer"] = this.nvStringLayer;
				}, err => {
					console.log(err)
				});
			},
