			getBath() {
				const hd_style = new Style({
					image: new Icon({
						src: require("../../assets/images/mapIcon/海洋浴场@3x.png")
					})
				});
				const marker = [];
				const name = "海水浴场";
				let coor = [];
				let coors = [];
				let jsonData = ''
				for (let i of this.typeList) {
					if (i.name == name) jsonData = i.file
				}
				jsonData = jsonData.replace(/^http:\/\/[^/]{3,255}/g, "");
				this.$axios.get(jsonData).then(
						res => {
							const features = res.data.features;
							// const num = features.length;
							// console.log("一共" + num + "个点");
							for (const feature of features) {
								const featureName = feature.attributes.站位编号;
								const tLng = feature.attributes.经度;
								const tLat = feature.attributes.纬度;
								const lng = feature.geometry.x;
								const lat = feature.geometry.y;
								coor = [lng, lat];
								const bath = [coor, featureName, tLng, tLat];
								if (this.buffFeature === null) {
									coors.push(bath);
								} else {
									const isIn = this.isIn(coor);
									if (isIn) {
										coors.push(bath);
									}
								}
							}
							for (const bath of coors) {
								let feature = new Feature({
									geometry: new Point(bath[0]),
									geometryName: bath[1],
									geometryName_: "bath",
									lng: bath[2],
									lat: bath[3]
								});
								marker.push(feature);
							}
							//#region 查询结果显示
							if (this.buffFeature !== null && coors.length !== 0) {
								const radius = this.radius;
								// this.$alert('<h3>空间查询成功</h3><br/><h3>一共有' + coors.length + '个监测站点在缓冲区内</h3>','查询结果',{dangerouslyUseHTMLString: true, center: true})
