										"查询结果",
										{center: true}
								);
								this.tableData = [];
								for (const bath of marker) {
									const name = bath.get("geometryName");
									let lng = bath.get("lng").toString();
									lng = this.getLngLat(lng);
									let lat = bath.get("lat").toString();
									lat = this.getLngLat(lat);
									const data = {name: name, lng: lng, lat: lat};
									this.tableData.push(data);
								}
								console.log(this.tableData);
								this.dialogTable = true;
							} else if (this.buffFeature !== null) {
								this.$alert("该区域内没有相关海水浴场", "查询结果", {
									center: true
								});
							}
							//#endregion
							this.baMarkers = marker;
							const vectorSource = new sVector({features: this.baMarkers});
							const layer = new Vector({
								title: "海水浴场",
								source: vectorSource,
								style: [hd_style]
							});
							this.baMarkerVectorLayer = new LayerGroup({
								title: "海水浴场",
								layers: [layer]
							});
							this.basemap.addLayer(this.baMarkerVectorLayer);
							this.siteOn = true;
							this.mapLayers["BathLayer"] = this.baMarkerVectorLayer;
						},
						err => {
							console.log(err);
						}
				);
				this.stationShow = true;
			},
