			getLayerByAttribute(layers, values) {
				let marker = new Feature();
				let layerName = "";
				if (layers === "site") {
					marker = this.markerVectorLayer;
					layerName = "监测站点";
				} else if (layers === "nuclear") {
					marker = this.nuMarkerVectorLayer;
					layerName = "核电站";
				} else if (layers === "preserves") {
					marker = this.preMarkerVectorLayer;
					layerName = "保护区";
				} else if (layers === "bath") {
					marker = this.baMarkerVectorLayer;
					layerName = "海水浴场";
				} else if (layers === "outfall") {
					marker = this.ofMarkerVectorLayer;
					layerName = "排污口";
				}
				this.basemap.removeLayer(marker);
				this.basemap.addLayer(marker);
				const vectorLayer = marker.getLayers().getArray()[0];
				const features = vectorLayer.getSource().getFeatures();
				const value = values;
				const property = "geometryName";

				for (const feature of features) {
					if (feature.get(property) === value) {
						this.selectedByAttriFeature = feature;
						break;
					}
				}
				if (this.selectedByAttriFeature !== null) {
					this.$alert("已找到name为:" + value + "的" + layerName, "提示", {
						center: true
					}).then(() => {
						const ani = this.$refs.ani;
						const aniOverlay = new Overlay({
							element: ani,
							positioning: "center-center"
						});
						let selectImg = "";
						if (layerName === "监测站点") {
							selectImg = require("../../assets/images/mapIcon/starSelect.png");
						} else if (layerName === "核电站") {
							selectImg = require("../../assets/images/mapIcon/nuclearSelect.png");
						} else if (layerName === "保护区") {
							selectImg = require("../../assets/images/mapIcon/preserveSelect.png");
						} else if (layerName === "海水浴场") {
							selectImg = require("../../assets/images/mapIcon/海洋浴场Select.png");
						} else if (layerName === "排污口") {
							selectImg = require("../../assets/images/mapIcon/排污口Select.png");
						}
						const pre_style = new Style({image: new Icon({src: selectImg})});
						this.selectedByAttriFeature.setStyle(pre_style);
						const center = this.selectedByAttriFeature.getGeometry().getExtent();
						const centerLng = center[0];
						const centerLat = center[1];
						const centerCor = [centerLng, centerLat];
						this.basemap.getView().animate({center: centerCor});
						this.aniShow = true;
						aniOverlay.setPosition(centerCor);
						this.basemap.addOverlay(aniOverlay);
						setTimeout(() => {
							this.basemap.removeOverlay(aniOverlay);
						}, 2050);
						this.attributeShow = false;
						this.attribute = "再次属性查询";
					});
				} else {
					this.$alert("没有找到name为:" + value + "的点", "提示", {
						center: true
					});
				}
