									siteIdUrl += "?site=" + featureName + "&type_name=water";
									this.siteId = featureName;
								}
								this.$axios.get(siteIdUrl).then(
										res => {
											let isTrue = false;
											let data = [];
											let paramList = [];
											paramList = JSON.parse(JSON.stringify(this.waterList));
											paramList.unshift({name: "评价结果", param: "result"});
											this.waterData = [];
											for (let i of res.data.results) {
												for (let v of i.site_data) {
													isTrue = v.water ? true : false;
												}
												if (isTrue) {
													this.waterData.push({
														id: i.id,
														label: i.BatchName,
