			showEnvironmentalMonitoring(name) {
				let url = "";
				var points = [];
				if (name == "水质评价数据") {
					url = "/api/detection_data/?latest=true";
				} else if (name == "沉积物评价结果") {
					url = "/api/sediment_data/?latest=true";
				} else if (name == "生物多样性评价") {
					url = "/api/ocean_diversity_data/?latest=true";
				}
				if (name !== "生物多样性评价") {
					this.$axios.get(url).then(res => {
						this.showEnviData = res.data.results;
						for (let i of this.showEnviData) {
							points.push({
								lat: i.lat,
								lng: i.lng,
								value: i[this.parameterValue]
							});
						}
						if (
								this.displayModeValue === "interpolation" &&
								points.length !== 0
						) {
							// this.showWater2(points);
							this.dataBar = true;
							this.vectorKriging(points)
						}
					});
				} else {
					this.$axios.get(url).then(res => {
						this.showEnviData = [];
						for (const data of res.data.results) {
							const site = data.site;
							const lng = site.lng;
							const lat = site.lat;
							const result = data.avg_grade; //评级结果
							let PhytoplanktonZooplanktonMesh = ""; //浮游植物
							let ZooplanktonAmount = ""; //浮游动物
							let BenthosRation = ""; //浅海大型底栖生物
							let TidalZoneBiologyRation = ""; //潮间带生物
							if (this.numsValue === "numberSpecies") {
								for (const tdData of data.data) {
									if (tdData.type === "浮游植物总物种数") {
										PhytoplanktonZooplanktonMesh = tdData.result;
									}
									if (tdData.type === "浮游动物总物种数") {
										ZooplanktonAmount = tdData.result;
									}
									if (tdData.type === "大型底栖生物总物种数") {
										BenthosRation = tdData.result;
									}
									if (tdData.type === "潮间带生物总物种数") {
										TidalZoneBiologyRation = tdData.result;
									}
								}
								if (PhytoplanktonZooplanktonMesh === "") {
									PhytoplanktonZooplanktonMesh = 0;
								}
								if (ZooplanktonAmount === "") {
									ZooplanktonAmount = 0;
								}
								if (BenthosRation === "") {
									BenthosRation = 0;
								}
								if (TidalZoneBiologyRation === "") {
									TidalZoneBiologyRation = 0;
								}
							}
							if (this.numsValue === "diversityindex") {
								for (const tdData of data.data) {
									if (tdData.type === "浮游植物物种多样性指数") {
										PhytoplanktonZooplanktonMesh = tdData.result;
									}
									if (tdData.type === "浮游动物种类多样性指数") {
										ZooplanktonAmount = tdData.result;
									}
									if (tdData.type === "大型底栖生物种类多样性指数") {
										BenthosRation = tdData.result;
									}
									if (tdData.type === "潮间带生物种类多样性指数") {
										TidalZoneBiologyRation = tdData.result;
									}
								}
								if (PhytoplanktonZooplanktonMesh === "") {
									PhytoplanktonZooplanktonMesh = 0;
								}
								if (ZooplanktonAmount === "") {
									ZooplanktonAmount = 0;
								}
								if (BenthosRation === "") {
									BenthosRation = 0;
								}
								if (TidalZoneBiologyRation === "") {
									TidalZoneBiologyRation = 0;
								}
							}
							this.showEnviData.push({
								lng: lng,
								lat: lat,
								result: result,
								PhytoplanktonZooplanktonMesh: PhytoplanktonZooplanktonMesh,
								ZooplanktonAmount: ZooplanktonAmount,
								BenthosRation: BenthosRation,
								TidalZoneBiologyRation: TidalZoneBiologyRation
							});
						}
						for (let i of this.showEnviData) {
							points.push({
								lat: i.lat,
								lng: i.lng,
								value: i[this.parameterValue]
							});
						}
						if (points.length !== 0 && this.numsValue !== "evaluationpinkScore") {
							this.dataBar = true;
							// this.showWater2(points);
							this.vectorKriging(points)
						} else {
							if (!!this.krigingLayers) {
								this.basemap.removeLayer(this.krigingLayers);
							}
							this.$alert("数据点小于3个，不能插值", "提示");
							this.dataBar = false;
						}
						if (this.numsValue === "evaluationpinkScore") {
							this.$alert("当前无法确定该数值", "提示");
						}
					}, err => {
						console.log(err)
					});
