							"#140000",
							"#000000"
						]
					}
					let dataset = {
						"type": "FeatureCollection",
						"features": []
					}
					//#region feature.value归一化处理
					for (let point of val) {
						const value = parseFloat(point.value === '' ? '0' : point.value)
						let level = value / this.krigingArr
						let feature = {
							"type": "Feature",
							"properties": {
								"level": level * 100
							},
							"geometry": {
								"type": "Point",
								"coordinates": [point.lng, point.lat]
							}
						}
						dataset.features.push(feature)
					}
					//#endregion
					//#region 克里金breaks设置
					const min = 0;
					const max = 100;
					const tabs = 100;//样本容量-1(真实breaks以0开始)
					const step = (max - min) / tabs;
					const breaks = min === max ? [min] : Array.from({length: tabs + 1}, (_, i) => min + (i * step));
					//#endregion
					//#region 矢量克里金插值
					let vectorKrig = freegis.kriging_contour(dataset, 'level', {
						model: params.krigingModel,
						sigma2: params.krigingSigma2,
						alpha: params.krigingAlpha
					}, breaks, params.extent, false)
					//#region vue裁剪，临时，将研究js裁剪
					let clip_features = vectorKrig.features.map(feature => {
						let turfClip = intersect(feature, params.clip_geom)
						if (turfClip !== null) {
							turfClip.properties = feature.properties
						}
						return turfClip
					})
					vectorKrig.features = clip_features.filter(d => d)
					//#endregion
					//#region 克里金level结果集获取
					let levels = []
					for (let feature of vectorKrig.features) {
						levels.push(feature.properties.level)
					}
					// console.log(levels)
					//#endregion
					//#region 等值线
					let krigingLine = vectorKrig.features.map(feature => {
						let line = polygonToLine(feature.geometry)
						line.properties = feature.properties
						return line
					})
					const linedataset = dataset
					let line = new GeoJSON().readFeatures(linedataset)
					linedataset.features = krigingLine.filter(d => d)
					let linesource = new sVector()
					linesource.addFeatures(line)
					let lineLayer = new Vector({
						source: linesource,
						style: function (feature, res) {
							let level = parseInt(feature.get('level')) / 10
							let color = params.colorL[level]
							let style = new Style({
								stroke: new Stroke({
									color: color,
									width: 4
								})
							})
							return [style]
						}
					})
					//#endregion
					let features = new GeoJSON().readFeatures(vectorKrig)
					let source = new sVector()
					source.addFeatures(features)
					let layer = new Vector({
						source: source,
						style: function (feature, res) {
							//得到点的权重，计算落在那个色度带
							let level = parseInt(feature.get('level')) / step;
							let colorN = params.colorN[level]//color for vector kriging
							let colorS = params.colorS[level]//color for contour
							let style = new Style({
								fill: new Fill({
									color: colorN
								}),
								stroke: new Stroke({
									color: colorN,
									width: 2
								})
							})
							return [style];
						}
					});
					this.krigingLayer = layer
					if (this.krigingLayer !== null) {
						this.krigingLayers = new LayerGroup({
							title: "克里金插值",
							layers: [this.krigingLayer]
						});
						this.basemap.addLayer(this.krigingLayers);
						this.mapLayers["KrigingLayer"] = this.krigingLayers;
						// this.showIsolined(val)
					}
					//#endregion
				}
			},
