				var slat = mapExtent[1] - (mapExtent[1] - mapExtent[3]) * 0.15;
				var pt = [slng, slat];
				var domid = "chart" + 99999;
				html +=
						"<div id='" +
						domid +
						"' style='width: 20px;height: 150px; margin-left: -2px;margin-bottom: -60px;'></div>";
