			showChart(params) {
				this.dataBar = true;
				if (this.eChartLayer !== null) {
					this.basemap.removeLayer(this.eChartLayer);
				}
				this.eChart = this.$refs.chart;
				let html = "";
				let inid = 0;
				let paraValue = [];
				for (const point of params) {
					const value = point.value;
					if (value !== "") {
						paraValue.push(parseFloat(value));
					}
				}
				const maxValueRaw = Math.max.apply(null, paraValue);
				const nn = Math.ceil(Math.abs(Math.log(maxValueRaw) / Math.log(10)));
				const maxValue =
						Math.ceil(maxValueRaw * Math.pow(10, nn)) / Math.pow(10, nn);
				const minValue = 0;
				for (const point of params) {
					const lng = point.lng;
					const lat = point.lat;
					const coor = [lng, lat];
					let value = point.value;
					if (value === "") {
						value = 0;
					}
					let domId = "chart" + inid;
					inid = inid + 1;
					html +=
							"<div id='" +
							domId +
							"'style='width: 20px;height: 150px; margin-left: -2px;margin-bottom: -60px;'></div>";
					this.eChart.innerHTML = html;
					const myChart = this.$echarts.init(document.getElementById(domId));
					const option = {
						tooltip: {
							trigger: "axis",
							axisPointer: {
								type: "shadow"
							}
						},
						xAxis: {
							name: "监测点名称",
							data: "1",
							show: false
						},
						yAxis: {
							max: "20",
							name: "2",
							show: false
						},
						series: [
							{
								name: "汞浓度(mg/kg)",

								type: "bar",
								itemStyle: {
									normal: {
										color: function (params) {
											return "#C1232B";
										},
										label: {
											show: true,
											position: "top",
											textStyle: {
												color: "blue",
												fontSize: 8
											},
											formatter: function () {
												return value;
											}
										}
									}
								},
								data: [((value - minValue) * 20) / (maxValue - minValue)]
							}
						]
					};
					myChart.setOption(option);
					this.eChartLayer = new Overlay({
						id: domId,
						element: document.getElementById(domId),
						offset: [0, 5],
						stopEvent: false
					});
					this.eChartLayer.setPosition(coor);
					this.basemap.addOverlay(this.eChartLayer);
					this.eChartLayer.setPositioning("bottom-center");
					this.showValueBar(maxValue, html);
				}
			},

        },
        components: {Map, ElImageViewer},
        watch: {
			parameterValue(val) {
				var points = [];
				for (let i of this.showEnviData) {
					points.push({lat: i.lat, lng: i.lng, value: i[this.parameterValue]});
				}
				if (val === "") {
					this.dataBar = false;
					this.resultPercent = false;
					if (this.krigingLayers !== null) {
						this.basemap.removeLayer(this.krigingLayers);
					}
				} else if (
						this.displayModeValue === "interpolation" &&
						points.length !== 0 &&
						this.numsValue !== "evaluationpinkScore"
				) {
					if (this.krigingLayers !== null) {
						this.basemap.removeLayer(this.krigingLayers);
					}
					if (this.eChartLayer !== null) {
						this.basemap.getOverlays().clear();
					}
					if (this.displayModeValue === "interpolation" && points.length !== 0) {
						// this.showWater2(points);
						this.vectorKriging(points)
						this.dataBar = true;
					}
					// this.showWater2(points);
					this.vectorKriging(points)
				} else if (this.displayModeValue === "histogram") {
					if (this.krigingLayers !== null) {
						this.basemap.removeLayer(this.krigingLayers);
					}
					this.showChart(points);
				} else if (this.numsValue === "evaluationpinkScore") {
					this.$alert("当前无法确定该数值", "提示");
				} else if (this.environmentalMonitoringValue === '生物多样性评价') {
					if (this.krigingLayers !== null) {
						this.basemap.removeLayer(this.krigingLayers);
					}
					this.showEnvironmentalMonitoring("生物多样性评价");
				}
			},
			displayModeValue(val) {
				const points = [];
				for (let i of this.showEnviData) {
					points.push({lat: i.lat, lng: i.lng, value: i[this.parameterValue]});
				}
				if (this.displayModeValue === "histogram") {
					if (this.krigingLayers !== null) {
						this.basemap.removeLayer(this.krigingLayers);
					}
					this.showChart(points);
				} else if (this.displayModeValue === "interpolation") {
					if (this.eChartLayer !== null) {
						this.basemap.getOverlays().clear(); //清除地图中所有Overlay
					}
					// this.showWater2(points);
					this.vectorKriging(points)
				}
			},
            numsValue(val) {
			    if (this.krigingLayers !== null) {
			        this.basemap.removeLayer(this.krigingLayers)
                }
			    this.showEnvironmentalMonitoring("生物多样性评价")
            }

        }
    }
</script>

